.game-element {
  height: 100vh;
  background: none

}
.hide {
  display:none;
}
.games-background {
  position: absolute;
  z-index: -1;
}
.background-img1{
  width:684px;
  height:684px;
  position:absolute;
  top:-100px;
  left:-100px;
  transform: rotate(15deg);
  opacity: .2;
}
.background-img2{
  width: 684px;
height: 684px;
position: absolute;
top: 50vh;
left: 70vh;
transform: rotate(-15deg);
opacity: 0.2;
}
.arrow-con {

  display: flex;
  align-items: center;
}

.arrow {
  width: 230px;
  height: 122px;
  color: #fbc666;
  background-color:   #ffffff;
  cursor: pointer;
  font-size: 50px;
  box-shadow: 0px 6px 16px 8px rgba(0, 0, 0, 0.08);
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}
.arrow:hover{transition-duration: .5s;  
  font-size: 70px;
}

.game-caro {
 

    height: 100vh !important;
}